  renderSortTwo() {
    const { UiActions, FormActions } = this.props;
    if (this.props.isSecondSortable) {
      UiActions.removeSecondSortable(); // -      .
      FormActions.resetSecondSortable();
    } else {
      if (this.props.form.get("productPosition") === "") {
        alert("   !");
      } else {
        UiActions.addSecondSortable(); // +      .
      }
    }
  }

