  calculateImageSize() {
    let totalSize = 0;
    this.props.form
      .toJS()
      .productImages.forEach(img => (totalSize += img.size));

    const MB = (totalSize / 1024 / 1024).toFixed(2);

    if (MB > 20) {
      return <span id="current_file_size--oversize">{MB}</span>;
    }

    return MB;
  }

  compressHandler = () => {
    const { FormActions } = this.props;
    const imageCompressor = new ImageCompressor();
    let newImages = [];

    const compressHelper = (file, option) =>
      imageCompressor
        .compress(file, option)
        .then(img => img)
        .catch(err => {
          // Handle the error
        });

    this.setState({
      isCompressing: true
    });
    async.each(
      this.props.form.toJS().productImages,
      (img, callback) => {
        const option = {
          quality: 0.6
        };

        compressHelper(img, option).then(compressedImg => {
          newImages.push(compressedImg);
          callback();
        });
      },
      done => {
        this.setState({
          isCompressing: false
        });
        FormActions.formUploadChange({
          formName: "product",
          name: "productImages",
          value: newImages
        });
        this.props.UiActions.showSweetAlert({
          value: "success",
          alertTitle: "",
          alertMessage: "text",
          message: " ."
        });
      }
    );
  };
