import React from 'react';
import { fromJS, Map } from 'immutable';

function DeliveryTable({form, sameCost, changeHandler, handleBlur, currentValue}) {
    const defaultValue = undefined;
    const currentVal = Map(currentValue).toJS();

    return (
        <table className="text-center col-md-6 col-xs-10 col-xs-offset-1 col-md-offset-0">
            <tbody>
                <tr>
                    <th className="text-center"></th>
                    <th className="text-center"></th>
                </tr>
                <tr>
                    <td>  </td>
                    <td><input
                            type="text"
                            name="SeoulGyungki"
                            placeholder="(,) "
                            value={currentVal.seoul_gyungki}
                            onChange={changeHandler}
                            onBlur={ form.get('isCostSame') ? handleBlur : defaultValue }
                        />
                    </td>
                </tr>
                {/*     input         */}
                <tr>
                    <td></td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="GangWon"
                            disabled={form.get('isCostSame')}
                            value={currentVal.gangwon}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td>  </td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="ChungNam"
                            disabled={form.get('isCostSame')}
                            value={currentVal.chungnam}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td></td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="ChungBuk"
                            disabled={form.get('isCostSame')}
                            value={currentVal.chungbuk}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td> </td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="GyeongBuk"
                            disabled={form.get('isCostSame')}
                            value={currentVal.gyeongbuk}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td>  </td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="GyeongNam"
                            disabled={form.get('isCostSame')}
                            value={currentVal.gyeongnam}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td></td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="JeonBuk"
                            disabled={form.get('isCostSame')}
                            value={currentVal.jeonbuk}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td> </td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="JeonNam"
                            disabled={form.get('isCostSame')}
                            value={currentVal.jeonnam}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
                <tr>
                    <td> </td>
                        { form.get('isCostSame') ? defaultValue : <td><input
                            type="text"
                            name="JeJuSanGan"
                            disabled={form.get('isCostSame')}
                            value={currentVal.jeju_sangan}
                            placeholder={form.get('isCostSame') ? sameCost: "(,) "}
                            onChange={changeHandler}
                        /></td> }
                        { form.get('isCostSame') ? <td><span>{sameCost}</span></td> : defaultValue }
                </tr>
            </tbody>
        </table>
    );
}

export default DeliveryTable;


