import React from 'react';

const firstSortEnum = [['', '', '', ''], // 
                       [' ', '', ' ', ' ', ' '], // 
                       ['', '', '', '', ''], // 
                       ['', '/', '()'], // /
                       ['', '()', '', '', ''], // /
                       ['', ' ', '  ']]; // 

const secondSortEnum = [[' ', ' ', ' ', ' '],
                        [' ', '', ' ', ' ', '', ''],
                        ['1 ', '2-3 ', '4 ', '5 ', ' ', '', ''],
                        ['TV', '&', '', ''],
                        ['2', '4', '6'],
                        ['&', '', ''],
                        [' ', ' ', ' ', ' ', ' '],
                        ['', '', '', '', '', '/', ' ', ' '],
                        [' ', '', '', '/ '],
                        ['', '3', '5', ''],
                        ['', ' ', '/'],
                        ['', '', ''],
                        ['', ''],
                        ['&', ' ', ' ', 'h ', ' ', ' ', ' '],
                        [' ', ' ', ' ', ' ', ' ', ' ', ' '],
                        ['1', '2', '3', '4', '5 ']];

function SortableSelect({isSecondSortable, first, sortIndex, changeHandler, position, formValue}) {
    if(first) {
        return (
            <select
                className="form-control"
                name={isSecondSortable ? 'firstSort_2' : 'firstSort_1'}
                onChange={changeHandler}
            >
                <option>1  </option>
            {firstSortEnum[sortIndex].map((value) => (
                <option key={value} value={value}>{value}</option>
            ))}
            </select>
        );
    } else {
        return (
            <select
                className="form-control"
                name={isSecondSortable ? 'secondSort_2' : 'secondSort_1'}
                onChange={changeHandler}
            >
                <option>2  </option>
            {secondSortEnum[sortIndex].map((value) => (
                <option key={value} value={value}>{value}</option>
            ))}
            </select>    
        );
    }
}

export default SortableSelect;


